body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  display: grid;
  place-items: center;
  height: 100vh;
}

header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: darkblue;
  color: white;
}

header nav{
  display: flex;
}

header nav h1 {
  margin: 0 auto 0 0;
  padding: 17px;
}

header ul {
  display: flex;
  margin-left: auto;
  padding: 0;
  margin: 0;
  list-style-type: none;
}

#nav-profile-pic {
  padding: 5px;
  border-radius: 100%;
}

#nav-email {
  padding: 20px;
  display: inline-block;
}

@media (max-width: 600px) {
  #nav-email {
    display: none;
  }
}

button {
  background-color: lightgray;
  font-size: 1rem;
  font-family: inherit;
  border-radius: 4px;
  padding: 10px;
  margin: 10px;
  display: inline-block;
  cursor: pointer;
}

main {
  text-align: center;
}

main#profile {
  text-transform: capitalize;
}

main#profile td {
  border-bottom: 1px solid lightgray;
  padding: 15px;

}

main#profile .profile-pic {
  border-radius: 100%;
}
